.page-num-active{color:#fff}.pagination-list{display:flex;align-items:center;justify-content:center}.pagination-link-dotted{cursor:auto}.pagination-item-disable{cursor:auto!important}.pagination-link-disable-hover{cursor:auto!important;opacity:.5}.pagination-item,.pagination-link-disable{cursor:pointer;-webkit-user-select:none;user-select:none;transition:all .4s!important;color:#d3d3d3;border-radius:.5rem;width:3.4rem;height:3.4rem;display:flex;justify-content:center;align-items:center;font-size:1.3rem;font-weight:700;margin:0 .3rem}.pagination-item p svg{margin-top:6px!important}.dotted-pagin{cursor:auto!important}.pagination-item:not(.pagination-item-disable,.dotted-pagin):hover,.pagination-link-disable:not(.pagination-link-disable-hover):hover{color:#000!important;background-color:#d3d3d3!important}.pagination-link--active{color:var(--white-color);background-color:var(--primary-color)}